@import "./variables.scss";
@import "./blocks/page.scss";

html {
    box-sizing: border-box;
}

.header.header-block {
    max-width: 1920px;
}

.header-block__black {
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    color: $white;
    background-color: $black;
}

.header-block__paragraph {
    font-size: 12px;
    line-height: 15px;
    margin-top: 0;
    padding-top: 7px;
    padding-bottom: 7px;
    letter-spacing: 2px;
}

.header-block__navline {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 23px;
    margin-left: 11px;
}

.header-block__nav-list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
}

.header-block__nav-link {
    color: $black;
    font-size: 14px;
    line-height: 21px;
    text-decoration: none;
    letter-spacing: 1px;
    margin-right: 22px;
    color: $black;
    text-decoration: none;
    text-transform: uppercase;

    &:hover {
        color: $orange;
        transition: 0.25s;
    }

    &:active {
        color: $dark-orange;
    }
}

.header-block__users {
    margin-right: 25px;
}

.header-block__img {
    position: relative;
    background-image: url("../img/header_img_fon@1x.jpg");
    width: 100%;
    background-color: darkgrey;
    background-size: cover;
    background-repeat: no-repeat;
}

.header-block__heading {
    font-size: 44px;
    line-height: 51px;
    margin: 0;
    margin-bottom: 14px;
    margin-left: auto;
    margin-right: auto;
    inline-size: fit-content;
    padding-top: 45px;
}

.header-block__subtitle {
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 1px;
    margin: 0;
    margin-bottom: 22px;
    margin-left: auto;
    margin-right: auto;
    inline-size: fit-content;
    padding-top: 0;
}

.header-block__link {
    padding: 5px 37px 5px 37px;
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 618px;
}

.button {
    text-decoration: none;
    border-radius: 35px;
    background-color: $orange;
    color: $white;
    display: inline-block;

    &:hover {
        transition: 0.3s;
        box-shadow: 0 0 10px $orange, 0 0 40px $orange;
    }

    &:active {
        background-color: $dark-orange;
        color: $midle-grey;
        box-shadow: inset 0px 3px 2px rgba(41, 44, 61, 0.34);
    }
}

.header-block__main--button {
    inline-size: fit-content;
    margin-left: auto;
    margin-right: auto;
}

.header-block__svg {
    margin-right: 122px;
    margin-top: 18px;
    margin-bottom: 34px;
}

.header-block__partners {
    background-color: $black;
    margin-bottom: 52px;
}

.header-block__svgblock {
    inline-size: fit-content;
    margin-left: auto;
    margin-right: auto;
}

.title-block__catalog {
    margin: 0;
    margin-bottom: 12px;
    font-size: 32px;
    line-height: 36px;
    text-align: center;
}

.title-block__subtitle {
    margin: 0;
    margin-bottom: 25px;
    font-size: 15px;
    line-height: 19px;
    text-align: center;
    max-width: 927px;
    margin-left: auto;
    margin-right: auto;
}

.catalog-img__boards {
    background-image: url("../img/boards-catalog.png");
    background-repeat: no-repeat;
    padding: 283px 169px 267px 170px;
    margin-right: 20px;
}

.catalog-img__revs {
    background-image: url("../img/revs-catalog.png");
    background-repeat: no-repeat;
    padding: 283px 169px 267px 170px;
}

.catalog-img {
    display: flex;
    justify-content: center;
}

.catalog-img__subtitle {
    font-size: 18px;
    line-height: 23px;
    text-align: center;
    margin: 0;
}

.catalog-img__title {
    font-size: 25px;
    line-height: 30px;
    text-align: center;
    margin: 0;
    margin-bottom: 14px;
}

.color {
    color: $white;
}

.catalog-shop {
    background-image: url("../img/electric_skateboards@1x.png");
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    @include retina {
        background-image: url("../img/electric_skateboards@2x.png");
    }
}

.catalog-shop {
    text-align: center;
    margin-bottom: 44px;
}

.catalog-shop__paragraph {
    padding-top: 44px;
    margin-bottom: 11px;
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 1px;
}

.catalog-shop__title {
    margin: 0;
    margin-bottom: 15px;
    font-size: 45px;
    line-height: 51px;
    font-weight: 700;
}

.catalog-shop__subtitle {
    margin: 0;
    margin-bottom: 20px;
    max-width: 488px;
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 1px;
    margin-right: auto;
    margin-left: auto;
}

.catalog-shop__link {
    padding: 5px 66px 5px 66px;
    margin-bottom: 505px;
}

.catalog-shop__img {
    margin-right: 20px;
}

.catalog-shop__img:nth-last-child(1) {
    margin-right: 40px;
}

.catalog-shop__rev {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 45px;
}

.catalog-shop__revtitle {
    font-size: 32px;
    line-height: 38px;
    margin: 0;
    margin-bottom: 21px;
}

.catalog-shop__revparagraph {
    max-width: 339px;
    font-size: 15px;
    line-height: 22px;
    margin: 0;
    margin-bottom: 18px;
}

.catalog-shop__revlink {
    padding: 5px 16px 5px 16px;
}

.catalog-shop__stealth--img {
    padding: 200px 120px 200px 120px;
    background-image: url("../img/sketboard@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @include retina {
        background-image: url("../img/sketboard@2x.jpg");
    }
}

.catalog-shop__title--variety {
    font-size: 25px;
    line-height: 30px;
}

.catalog-shop__subtitle--variety {
    font-size: 18px;
    line-height: 23px;
}

.catalog-shop__plus--img {
    padding: 200px 120px 200px 120px;
    background-image: url("../img/sketboard_men@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    @include retina {
        background-image: url("../img/sketboard_men@2x.jpg");
    }
}

.catalog-shop__mini--img {
    padding: 200px 120px 200px 120px;
    background-image: url("../img/friends_skate@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @include retina {
        background-image: url("../img/friends_skate@2x.jpg");
    }
}

.catalog-shop__variety {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    text-align: center;
    margin-bottom: 55px;
}

.wrapper {
    display: flex;
    justify-content: center;
    margin-bottom: 64px;
}

.wrapper__video {
    position: relative;
    padding: 0 0 57%;
    min-width: 540px;
}

.wrapper__video iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.accessories {
    min-width: 368px;
    margin: 0 auto;
}

.accessories__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 36px;
    text-align: center;
}

.accessories__title::before {
    content: "";
    display: block;
    width: 52px;
    height: 2px;
    margin-right: 7px;
    transform: translate(-27px, -8px) rotate(90deg);
    background-color: $light-black;
}

.catalog-list__title {
    margin: 0;
    margin-bottom: 3px;
    font-size: 15px;
    line-height: 18px;
    text-align: center;
    color: $orange;
}

.catalog-list__price {
    margin: 0;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    color: $black;
}

.catalog-list__price--color {
    color: $orange;
}

.catalog-list__transparent {
    opacity: 0.3;
}

.catalog-list {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    inline-size: fit-content;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
}

.catalog-list__item {
    margin-right: 20px;
    list-style: none;
}

.low-bord {
    height: 66px;
    background: rgba(255, 255, 255, 0.9);
    box-shadow: 2px 0px 10px rgba(41, 44, 61, 0.34);
    border-radius: 0px;
    margin-bottom: 28px;
}

.opportunities {
    background-color: $black;
}

.opportunities__title {
    color: $white;
    font-size: 31px;
    line-height: 36px;
    font-weight: 700;
    margin: 0;
}

.opportunities__list {
    text-decoration: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.opportunities__description {
    margin: 0;
    color: $white;
    max-width: 270px;
}

.opportunities__item {
    text-align: center;
    margin-right: 72px;
    padding: 35px 0px 51px 0px;
}
.opportunities__item:nth-last-child(1) {
    margin-right: 0;
}

.footer-section__title {
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
    margin-bottom: 10px;
}

.footer-section__about {
    font-size: 19px;
    line-height: 30px;
    font-weight: 400;
    margin-bottom: 12px;
}

.footer-section__list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.footer-section__item {
    font-size: 13px;
    line-height: 21px;
    cursor: pointer;

    &:hover {
        color: $dark-orange;
        transition: 0.25s;
    }
}

.footer-section__description {
    font-size: 14px;
    line-height: 21px;
    max-width: 367px;
    margin-bottom: 19px;
}

.footer-section {
    display: flex;
    background-color: $midle-grey;
    margin-bottom: 19px;
}

.footer-section__logo {
    margin: 50px 240px 130px 145px;

    &:hover {
        fill: $orange;
    }
}

.footer-section__explore {
    margin-right: 256px;
}

.payment__orange {
    color: $orange;
}

.payment {
    display: flex;
    align-items: center;
    justify-content: center;
}

.payment__paragraph {
    margin-right: 446px;
}

.payment {
    margin-bottom: 32px;
}

.payment__icon {
    cursor: pointer;
}

.header-block__logo {
    fill: $dark-orange;
}
